import { Subscription } from 'rxjs';
import { AuthService } from './../../shared/guard/auth.service';
import { ActivatedRoute, Router } from '@angular/router';
import { Component, OnInit, OnDestroy } from '@angular/core';

@Component({
  selector: 'app-acounts-core',
  templateUrl: './acounts-core.component.html',
  styleUrls: ['./acounts-core.component.scss']
})
export class AcountsCoreComponent implements OnInit, OnDestroy {
  subscription1: Subscription;
  isLoggedIn = false;
  constructor(
    private activatedRoute: ActivatedRoute,
    private router: Router,
    private authService: AuthService,
  ) {
    this.subscription1 = this.authService.castLoggedIn.subscribe(
      islogin => (this.isLoggedIn = islogin)
    );
    setTimeout(() => {
      if (this.isLoggedIn) {
        this.router.navigate(['/home']);
      }
     }, 100);
  }
  ngOnInit(): void {
  }
  ngOnDestroy() {
    this.subscription1.unsubscribe();
  }
}
