  // tslint:disable-next-line: variable-name
  getPlan(account_type) {
    const data: any = {
      oAccount_Type: account_type,
      DealerId: 1001
    };
    this.global.getPackage(data).subscribe((accountType: any) => {
      accountType.find(plan => {
        this.planId = plan.sAccountPlanId;
        // tslint:disable-next-line: triple-equals
        if (this.planId == 1000) {
          this.getLeverage();
        }
      });
    }, err => {
      console.log(err);
    });
  }

  getLeverage() {
    const data: any = {
      PackageId: 1000
    };
    this.global.getLeverageByPackage(data).subscribe((pkg: any) => {
      console.log(pkg);
      this.allPackage = pkg;
    }, err => {
      console.log(err);
    });
  }

  userRegistration(registration) {
    this.isLoading = true;
    const userData: any = {
      uSignup_Profile:
      {
        sMasterId: 0,
        sWalletId: 0,
        uReferredBy: 0,
        sDealerId: 1001,
        uPersonalInfo:
        {
          First: registration.form.value.name,
          Last: registration.form.value.lastname,
          ProfilePic: './assets/default.png',
          Comment: ' '
        },
        uContactInfo: {
          Email: registration.form.value.email,
          Phone: registration.form.value.phone,
          Email_Verify: 0,
          Phone_Verify: 0
        },
        uAddressInfo: {
          Address1: ' ',
          Address2: ' ',
          City: ' ',
          State: ' ',
          Country: registration.form.value.country,
          ZipCode: ' '
        },
        uKYCInfo: {
          ID_PRROF: '',
          ADDR_PROOF: '',
          Others: ''
        },
        uTIMESTAMP_INFO: {
          sCreadedOn: 0,
          sUpdatedOn: 0,
          sCreadedOn_Str: new Date(),
          sUpdatedOn_Str: new Date()
        },
        oTRNx_DSH_Permit: [],
        oTRNx_MAM_Permit: [],
        oTRNx_PAMN_Permit: [],
        oTRNx_IB_Permit: [],
        oTRNx_AFL: [],
        oSignType: 3
      },
      uPlanId: this.planId,
      BonusCard: 0,
      uInitialDep:  Number(this.demoAmount),
      uLeverage: this.leverage,
      isIBAllow: this.queryParam ? true : false,
      ReferCode: this.queryParam ? this.queryParam : 0,
      isAffiliateAllow: this.affiliateReferCode ? true : false,
      Aff_Code: this.affiliateReferCode ? this.affiliateReferCode : 0,
    };
    this.global.makeSignupVerification(userData).subscribe((user: any) => {
      console.log(user);
      if (user.Result) {
        this.isLoading = false;
        this.userRegister = user;
        this.register = true;
        this.notregister = false;
        setTimeout(() => { this.register = false; }, 10000);
        this.notificationService.showSuccess('You have Signed up successfully', 'success');
        this.notificationService.showInfo('Verification mail has been send to your registered email Id !', 'Info');
        this.router.navigate(['../otp-password'], {relativeTo: this.route});
      } else {
        this.isLoading = false;
        this.notregister = true;
        this.register = false;
        this.notificationService.showError('Email address is already exists !', 'Error');
        setTimeout(() => { this.notregister = false; }, 10000);
      }
    },
    err => {
        console.log(err);
        this.notificationService.showError('Faild to Signup Process', 'Error');
        this.isLoading = false;
    });
  }
  numberOnly(event): boolean {
    const charCode = (event.which) ? event.which : event.keyCode;
    if (charCode > 31 && (charCode < 48 || charCode > 57)) {
      this.numericMessage = true;
      return false;
    }
    this.numericMessage = false;
    return true;
  }
