import { AcountsCoreComponent } from './acounts-core/acounts-core.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { SetPasswordComponent } from './acounts-core/set-password/set-password.component';
import { ForgetPasswordComponent } from './acounts-core/forget-password/forget-password.component';
import { OtpPasswordComponent } from './acounts-core/otp-password/otp-password.component';
import { SignupComponent } from './acounts-core/signup/signup.component';
import { LoginComponent } from './acounts-core/login/login.component';
import { SetPassword1Component } from './auth-core/set-password1/set-password1.component';
import { OtpPassword1Component } from './auth-core/otp-password1/otp-password1.component';

import { AuthCoreComponent } from './auth-core/auth-core.component';
import { Login1Component } from './auth-core/login1/login1.component';
import { Signup1Component } from './auth-core/signup1/signup1.component';
import { ForgetPassword1Component } from './auth-core/forget-password1/forget-password1.component';

const routes: Routes = [
  {
    path: '',
    component: AcountsCoreComponent,
    children: [
      {
        path: '',
        pathMatch: 'full',
        redirectTo: 'login'
      },
      {
        path: 'login',
        component: LoginComponent,
        data: {breadcrumb: 'login'}
      },
      {
        path: 'signup',
        component: SignupComponent,
        data: {breadcrumb: 'signup'}
      },
      {
        path: 'otp-password',
        component: OtpPasswordComponent,
        data: {breadcrumb: 'otp-password'}
      },
      {
        path: 'forget-password',
        component: ForgetPasswordComponent,
        data: {breadcrumb: 'Forget password'}
      },
      {
        path: 'set-password',
        component: SetPasswordComponent,
        data: {breadcrumb: 'Set Password'}
      },
    ],
  },
  {
    path: 'style1',
    component: AuthCoreComponent,
    children: [
      {
        path: '',
        pathMatch: 'full',
        redirectTo: 'login1'
      },
      {
        path: 'login1',
        component: Login1Component,
        data: {breadcrumb: 'login'}
      },
      {
        path: 'signup1',
        component: Signup1Component,
        data: {breadcrumb: 'signup'}
      },
      {
        path: 'otp-password1',
        component: OtpPassword1Component,
        data: {breadcrumb: 'Otp Password'}
      },
      {
        path: 'forget-password1',
        component: ForgetPassword1Component,
        data: {breadcrumb: 'Forget-Password'}
      },
      {
        path: 'set-password1',
        component: SetPassword1Component,
        data: {breadcrumb: 'Set Password'}
      },
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AccountsRoutingModule { }
