import { NotificationService } from './../../../shared/services/notification.service';
import { GlobalService } from './../../../shared/services/global.service';
import { Router, ActivatedRoute } from '@angular/router';
import { NgForm } from '@angular/forms';
import { Subscription } from 'rxjs';
import { Component, OnInit, ViewChild, OnDestroy } from '@angular/core';

@Component({
  selector: 'app-forget-password',
  templateUrl: './forget-password.component.html',
  styleUrls: ['./forget-password.component.scss']
})
export class ForgetPasswordComponent implements OnInit, OnDestroy {
  LoggedParams: any;
  subscription1: Subscription;
  isLoading = false;
  @ViewChild('formData', {static: false}) resetPasswordForm: NgForm;
  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private globalService: GlobalService,
    private notificationService: NotificationService
  ) { }

  ngOnInit(): void {
  }
  onSubmit() {
    this.isLoading = true;
    const params = {
      Email: this.resetPasswordForm.value.useremail,
      DealerId: 1001,
    };
    this.globalService.passRequest(params).subscribe(data => {
      if (data.Result === true) {
        this.isLoading = false;
        this.notificationService.showSuccess(data.MSG_USER, 'success');
        this.router.navigate(['../login'], {relativeTo: this.route});
      } else {
        this.isLoading = false;
        this.notificationService.showError(data.MSG_USER, 'Error');
      }
    },
    err => {
      console.log(err);
      this.notificationService.showError('Faild to Reset Password', 'Error');
      this.isLoading = false;
    });
  }
  ngOnDestroy() {
  }
}
