  logValidationErrorPasswors(group: FormGroup = this.frmPassword): void {
    Object.keys(group.controls).forEach((key: string) => {
      const abstractControl = group.get(key);
      // console.log(">>abstractControl", abstractControl)
      if (abstractControl instanceof FormGroup) {
        this.logValidationErrorPasswors(abstractControl);
      } else {
        this.formErrorsPassword[key] = '';
        if (abstractControl && !abstractControl.valid
          && (abstractControl.touched || abstractControl.dirty)) {
          const messages = this.errorMessagesPassword[key];
          console.log(">>abstractControl.errors", abstractControl.errors)
          for (const errorKey in abstractControl.errors) {
            if (errorKey) {
              this.formErrorsPassword[key] += messages[errorKey] + ' ';
            }
          }
        }
      }
    });
  }
  markFormTouched(formGroup: FormGroup) {
    (<any>Object).values(formGroup.controls).forEach(control => {
      if (control.controls) { 
        // control is a FormGroup
        this.markFormTouched(control);
      } else { 
        // control is a FormControl
        control.markAsTouched();
      }
    });
  }
