import { NotificationService } from './../../../shared/services/notification.service';
import { Component, OnInit, ElementRef, ViewChild } from '@angular/core';
import { LoginModalData } from './../../../shared/models/login.modal';
import { GlobalService } from './../../../shared/services/global.service';
import { AuthService } from './../../../shared/guard/auth.service';
import { Router, ActivatedRoute } from '@angular/router';
import { NgForm } from '@angular/forms';
import { HeaderService } from './../../../shared/services/header.service';
@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {
  @ViewChild('formData', {static: false}) signInForm: NgForm;
  userLoginDetail = new LoginModalData();
  // submitted = false;
  isLoading = false;
  // loginError = false;
  constructor(
    private globalService: GlobalService,
    private router: Router,
    private authService: AuthService,
    private activatedRoute: ActivatedRoute,
    private notificationService: NotificationService
    ) {}
    onLoginSubmit() {
      this.isLoading = true;
      this.userLoginDetail.Email = this.signInForm.value.useremail;
      this.userLoginDetail.Password = this.signInForm.value.userpassword;
      this.globalService.loginUser(this.userLoginDetail).subscribe(
        usersData => {
          console.log(usersData);
          if (usersData.sProfile.sDealerId !== 0) {
            this.SetDataToLocalStorage(usersData);
            this.authService.login(true);
            // this.loginError = false;
            this.router.navigate(['/home']);
            this.notificationService.showSuccess('You have Successfully logged in', 'Success', 500);
            this.isLoading = false;
          } else {
            // this.loginError = true;
            this.authService.login(false);
            this.notificationService.showError('Please enter correct email & password !', 'Error');
            this.isLoading = false;
          }
        },
        err => {
          console.log(err);
          this.notificationService.showError('Failed to login!', 'Error');
          this.isLoading = false;
        }
      );
    }
  ngOnInit(): void {}

  // Set User Data in Local storage
  SetDataToLocalStorage(usersData: any) {
    localStorage.clear();
    localStorage.setItem('LoggedDealerCode', JSON.stringify(usersData.sProfile.sDealerId));
    localStorage.setItem('LoggedDealerEmail', usersData.sProfile.uContactInfo.Email );
    localStorage.setItem('LoggedDealerName', usersData.sProfile.uPersonalInfo.First);
    localStorage.setItem('LoggedDealermasterid', usersData.sProfile.sMasterId);
    localStorage.setItem('LoggedDealerwalletID', usersData.sProfile.sWalletId);
    localStorage.setItem('LoggedUser', JSON.stringify(usersData));
  }

}
